<template>
<!-- 待办主页面 -->
  <div class="wait-dealt-index">
    <h1>待办</h1>
    <div class="workbenchTitle_dropdown" style="padding-bottom: 3%;padding-top: 2%;">
        <van-dropdown-menu :overlay="false">
            <van-dropdown-item v-model="value" :title='title' :options="option" @change='handelChange'/>
        </van-dropdown-menu>
    </div>
    <!-- <van-tabs v-model="active" color='#0247fe' title-active-color='#0247fe'>
      <van-tab title="质量验收审批 3" name="a">
      </van-tab>
      <van-tab title="问题管理待办 3" name="b">
      </van-tab>
      <van-tab title="质量检查确认 3" name="c">
      </van-tab>
      <van-tab title="项目信息3" name="d">
      </van-tab>
      <van-tab title="项目信息4" name="e">
      </van-tab>
      <van-tab title="项目信息5" name="f">
      </van-tab>
    </van-tabs> -->
    <div class="dealt-list">
      <van-cell v-for="(item, i) in waitDealtData" :key="i" is-link @click="goWaitDealtList(item)">
        <template #icon>
          <img :src="item.svgUrl">
        </template>
        {{item.waitDealtName}}
        <template #right-icon>
          <span v-show="item.val" :style="item.val ? 'color: #f20909' : ''">{{item.val}}</span>
        </template>
      </van-cell>
    </div>  
  </div>
</template>

<script>
export default {
  name: 'WaitDealtIndex',
  data() {
    return {
      value: '',
      title: '',
      option: [],
      active: 'a',
      examine: require('@/assets/close.svg'),
      waitDealtData: [
        {svgUrl: require('@/assets/waitDealtSvg/planManage.svg'), waitDealtName: '方案管理', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/entrust.svg'), waitDealtName: '委托设计', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/problemManage.svg'), waitDealtName: '问题管理', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/overhaulSpecial.svg'), waitDealtName: '检修专项排查', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/overhaulPlanning.svg'), waitDealtName: '检修统筹计划', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/overhaulManageManual.svg'), waitDealtName: '大检修管理手册', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/overhaulNormalizeManage.svg'), waitDealtName: '大检修规范化管理', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/noOverhaulPlan.svg'), waitDealtName: '非检修项目计划', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/overhaulCheck.svg'), waitDealtName: '交检修前单项验收', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/stopOverhaulConfirm.svg'), waitDealtName: '停工交检修确认单', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/qualityTesting.svg'), waitDealtName: '过程质量检查', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/projectFinish.svg'), waitDealtName: '项目完工验收', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/singleFinish.svg'), waitDealtName: '交开工前单项验收', val: 0},
        {svgUrl: require('@/assets/waitDealtSvg/overhaulStartConfirm.svg'), waitDealtName: '检修叫开工确认单', val: 0}
      ],
    }
  },
  computed: {
  },
  methods: {
    handelChange (val) {
      this.option.forEach(obj => {
        if (obj.value === val) {
          this.title = obj.text
          this.$store.commit('changeVersionId', val);
        }
      })
      this.$bus.$emit('handleVersion')
      this.getWaitList()
    },
   goWaitDealtList(item) {
    this.$router.push({name: 'WaitDealtList', query: item})
   },
   getWaitList() {  //  获取待办数据
   this.waitDealtData.forEach(ele=> { //  重置操作
    ele.val = 0
   })
    console.log('userId', JSON.parse(window.localStorage.getItem('userId')))
    console.log('userCookie', JSON.parse(window.localStorage.getItem('userCookie')))
      let userCookie  = JSON.parse(window.localStorage.getItem('userCookie'))
      this.$getHttp(`${this.$baseUrl}/OS-PM/versions/${this.$store.getters.getVersionId ? this.$store.getters.getVersionId : window.localStorage.getItem('versionId')}/backlogsCount`).then(res=> { //  
        // debugger
        console.log('待办主页面', res)
        if (res.status == 200) {
          Object.keys(res.data).forEach(item=> {
            // debugger
            if (res.data[item] > 0) {
              this.waitDealtData.forEach(ele=> {
                // debugger
                if (ele.waitDealtName == item || (item == '检修方案' && ele.waitDealtName == '方案管理') || (item == '检修手册' && ele.waitDealtName == '大检修管理手册')) {
                  ele.val = res.data[item]
                }
              })
            }
            // this.waitDealtData.push({waitDealtName: item, val: res.data[item]})
          })
          function compare(property) {  //  比较函数
            return function(a, b) {
              let val1 = a[property]
              let val2 = b[property]
              return val2 - val1
            }
          }
          this.waitDealtData.sort(compare('val'))
        }
      }).catch(err=> {
        // debugger
      })
   }
  },
   async created () {
    const res = await this.$getHttp(`${this.$baseUrl}/OS-PM/version/getAllVersion`, {})
    const optionArray = []
    res.data.forEach(e => {
      const obj = {
        text: e.versionName,
        value: e.id
      }
      optionArray.push(obj)
    })
    this.option = optionArray;
    this.value = window.localStorage.getItem('versionId');
  },
  mounted() {
    this.getWaitList();
    this.$bus.$emit('handleVersion')
  }
}
</script>

<style lang='less' scoped>
  * {
    padding: 0;
    margin: 0;
  }
  .wait-dealt-index {
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    overflow: hidden;
    height: 100vh;
    padding-right: 0rem;
    h1 {
      font-weight: 500;
      font-size: 1.375rem;
    }
    .dealt-list {
      height: calc(100% - 9.375rem);
      overflow: auto;
      box-sizing: border-box;
      padding-right: 1rem;
      .van-cell {
        padding: 1rem 0rem !important;
        img {
          vertical-align: middle;
          margin-right: 1.3rem;
        }
        span {
          font-size: 0.875rem;
          color: #808187;
          padding: 0.125rem 0.5rem;
          background: #F3F3F3;
        }
      }
    }



    .workbenchTitle_dropdown{
        width: 50%;
        height: 1.3rem;
        line-height:1.3rem;
        // margin: 0 0 0 1rem
        margin: 0.3rem 0 0 0rem
    }
    /deep/ .van-dropdown-menu{
        height: 1.3rem
    }
    /deep/ .van-dropdown-menu__bar{
        height: 1.3rem
    }
    /deep/ .van-dropdown-menu__item {
        justify-content: flex-start;
        height: 1.3rem
    }
    /deep/ .van-dropdown-menu__title{
        padding-left: 0;
    }
    ::v-deep .van-dropdown-menu {
      width: 90%;
      display: inline-block;
      .van-dropdown-menu__bar {
        border-radius: 0.5rem;
        box-shadow: none
      }
      .van-dropdown-menu__title::after {
        right: -6.25rem
      }
    }
  }
</style>